import React from 'react';
import { TransactionData } from '../AddTransactionModal/AddTransactionModal';
import './RecentTransactions.css';

interface RecentTransactionsProps {
  transactions: TransactionData[];
}

const RecentTransactions: React.FC<RecentTransactionsProps> = ({ transactions }) => {
  // Only show the last 5 transactions
  const recentTransactions = transactions.slice(0, 5);

  // If no transactions, don't render anything
  if (recentTransactions.length === 0) {
    return null;
  }

  const getCategoryIcon = (category: string) => {
    const icons: { [key: string]: string } = {
      bills: '',
      food: '',
      baby: '',
      house: '',
      car: '',
      entertainment: '',
      income: '',
      salary: ''
    };
    return icons[category] || '';
  };

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffTime = Math.abs(now.getTime() - date.getTime());
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));

    if (diffDays === 1) return 'Today';
    if (diffDays === 2) return 'Yesterday';
    if (diffDays <= 7) return `${diffDays - 1} days ago`;
    return date.toLocaleDateString();
  };

  return (
    <div className="recent-transactions">
      <div className="transaction-list">
        {recentTransactions.map((transaction, index) => (
          <div key={transaction.id || index} className="transaction-item">
            <div
              className={`transaction-icon ${transaction.type}`}
              style={{
                background: transaction.type === 'income'
                  ? 'rgba(16, 185, 129, 0.15)'
                  : 'rgba(249, 115, 22, 0.15)'
              }}
            >
              {getCategoryIcon(transaction.category)}
            </div>
            <div className="transaction-details">
              <div className="transaction-name">{transaction.description || `${transaction.category} transaction`}</div>
              <div className="transaction-category">{transaction.category}</div>
            </div>
            <div className="transaction-meta">
              <div className="transaction-date">{formatDate(transaction.createdAt || transaction.date)}</div>
              <div className={`transaction-amount ${transaction.type}`}>
                {transaction.type === 'expense' ? '-' : '+'}{transaction.amount}
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default RecentTransactions;
