import React, { useState, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import { createAuthenticatedApi } from '../../utils/api';
import SpendingChart from '../../components/SpendingChart/SpendingChart';
import Categories from '../../components/Categories/Categories';
import RecentTransactions from '../../components/RecentTransactions/RecentTransactions';
import AddTransactionModal, { TransactionData } from '../../components/AddTransactionModal/AddTransactionModal';
import './Home.css';

const Home: React.FC = () => {
  const { user, accessToken, refreshToken } = useAuth();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [showResetModal, setShowResetModal] = useState(false);
  const [transactions, setTransactions] = useState<TransactionData[]>([]);

  // Create authenticated API instance
  const api = React.useMemo(() =>
    createAuthenticatedApi(
      () => accessToken || '',
      refreshToken
    ),
    [accessToken, refreshToken]
  );

  // Load transactions from API on component mount
  useEffect(() => {
    const loadTransactions = async () => {
      if (!user) return;

      try {
        const response = await api.get('/expenses');
        if (response.success) {
          // Transform API data to match component expectations
          const transformedTransactions = response.data.expenses.map((expense: any) => ({
            id: expense.id,
            type: expense.type,
            amount: expense.amount,
            category: expense.category,
            description: expense.description,
            date: expense.date,
            createdAt: expense.createdAt
          }));
          setTransactions(transformedTransactions);
        }
      } catch (error) {
        console.error('Error loading transactions:', error);
        setTransactions([]);
      }
    };

    loadTransactions();
  }, [user, api]);

  const handleOpenModal = () => {
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
  };

  const handleTransactionSubmit = async (transactionData: TransactionData) => {
    try {
      const response = await api.post('/expenses', transactionData);

      if (response.success) {
        // Add the new transaction to the local state
        const newTransaction = {
          id: response.data.expense.id,
          type: response.data.expense.type,
          amount: response.data.expense.amount,
          category: response.data.expense.category,
          description: response.data.expense.description,
          date: response.data.expense.date,
          createdAt: response.data.expense.createdAt
        };
        setTransactions(prev => [newTransaction, ...prev]);
      }
    } catch (error) {
      console.error('Error creating transaction:', error);
      alert('Failed to create transaction. Please try again.');
    } finally {
      setIsModalOpen(false);
    }
  };

  const handleResetConfirm = async () => {
    try {
      // Delete all transactions from backend
      for (const transaction of transactions) {
        if (transaction.id) {
          await api.delete(`/expenses/${transaction.id}`);
        }
      }

      // Clear local state
      setTransactions([]);
      alert('All transactions have been deleted successfully');
    } catch (error) {
      console.error('Error resetting transactions:', error);
      alert('Failed to reset transactions. Please try again.');
    } finally {
      setShowResetModal(false);
    }
  };

  const handleResetCancel = () => {
    setShowResetModal(false);
  };

  // Calculate summary values from transactions
  const calculateSummary = () => {
    const totalIncome = transactions
      .filter(t => t.type === 'income')
      .reduce((sum, t) => sum + t.amount, 0);

    const totalExpenses = transactions
      .filter(t => t.type === 'expense')
      .reduce((sum, t) => sum + t.amount, 0);

    const netBalance = totalIncome - totalExpenses;

    return {
      totalIncome,
      totalExpenses,
      netBalance
    };
  };

  const summary = calculateSummary();

  return (
    <div className="home">
      <div className="container">
        {/* Page Title */}
        <div className="page-header">
          <h1>Your Spending</h1>
          <p>Track and manage your expenses efficiently</p>
        </div>

        {/* Main Layout */}
        <div className="layout">
          {/* Left Column */}
          <div className="left-column">
            {/* Spending Overview Section */}
            <div className="section">
              <SpendingChart transactions={transactions} />
            </div>

            {/* Recent Transactions Section */}
            <div className="section transactions-section">
              <div className="section-title">Recent Transactions</div>
              <RecentTransactions transactions={transactions} />
            </div>
          </div>

          {/* Right Column */}
          <div className="right-column">
            {/* Summary Cards */}
            <div className="section">
              <div className="section-title">This Month</div>
              <div className="summary-card">
                <div className="summary-item">
                  <div className="summary-label">Total Income</div>
                  <div className="summary-value income">{summary.totalIncome.toLocaleString()}</div>
                </div>
                <div className="summary-item">
                  <div className="summary-label">Total Expenses</div>
                  <div className="summary-value expense">{summary.totalExpenses.toLocaleString()}</div>
                </div>
              </div>
              <div style={{ marginTop: '16px' }}>
                <div className="summary-item">
                  <div className="summary-label">Net Balance</div>
                  <div className={`summary-value ${summary.netBalance >= 0 ? 'balance' : 'expense'}`}>
                    {summary.netBalance >= 0 ? '+' : ''}{summary.netBalance.toLocaleString()}
                  </div>
                </div>
              </div>
            </div>

            {/* Categories */}
            <div className="section">
              <div className="section-title">
                <span>Categories</span>
                <button
                  className="reset-button"
                  onClick={() => setShowResetModal(true)}
                  title="Reset monthly values"
                >
                  Reset
                </button>
              </div>
              <Categories transactions={transactions} onReset={() => setShowResetModal(true)} />
            </div>

            {/* Action Buttons */}
            <div className="action-buttons">
              <button className="btn btn-primary" onClick={handleOpenModal}>+ Add Transaction</button>
              <button className="btn btn-secondary">View Budgets</button>
              <button className="btn btn-secondary">Export Data</button>
            </div>
          </div>
        </div>
      </div>

      {/* Add Transaction Modal */}
      <AddTransactionModal
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        onSubmit={handleTransactionSubmit}
      />

      {/* Reset Confirmation Modal */}
      {showResetModal && (
        <div className="reset-modal-overlay" onClick={handleResetCancel}>
          <div className="reset-modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="reset-modal-header">
              <h3 className="reset-modal-title">Reset Monthly Values</h3>
            </div>
            <div className="reset-modal-body">
              <p>Are you sure you want to reset the monthly values?</p>
              <p className="reset-modal-warning">This action cannot be undone.</p>
            </div>
            <div className="reset-modal-footer">
              <button className="reset-modal-btn reset-modal-cancel" onClick={handleResetCancel}>
                No
              </button>
              <button className="reset-modal-btn reset-modal-confirm" onClick={handleResetConfirm}>
                Yes
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Home;
