
class ApiClient {
  private baseURL: string;

  constructor() {
    this.baseURL = process.env.REACT_APP_API_URL || '';
  }

  private async makeRequest(
    endpoint: string,
    options: RequestInit = {},
    retryCount = 0
  ): Promise<any> {
    const url = `${this.baseURL}${endpoint}`;

    // Get auth context (this is a bit tricky in a utility function)
    // We'll need to pass the auth functions as parameters or use a different approach

    const config: RequestInit = {
      ...options,
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
      credentials: 'include', // Important for cookies
    };

    // Add authorization header if we have a token
    // This will be handled by the calling component using useAuth

    try {
      const response = await fetch(url, config);

      // If we get a 401, try to refresh token once
      if (response.status === 401 && retryCount === 0) {
        try {
          // This would need to be handled by the component using the API
          // For now, we'll just return the error
          console.log('401 received, token might need refresh');
        } catch (refreshError) {
          console.error('Token refresh failed:', refreshError);
        }
      }

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || `HTTP error! status: ${response.status}`);
      }

      return data;
    } catch (error) {
      console.error('API request failed:', error);
      throw error;
    }
  }

  async get(endpoint: string): Promise<any> {
    return this.makeRequest(endpoint, { method: 'GET' });
  }

  async post(endpoint: string, data?: any): Promise<any> {
    return this.makeRequest(endpoint, {
      method: 'POST',
      body: data ? JSON.stringify(data) : undefined,
    });
  }

  async put(endpoint: string, data?: any): Promise<any> {
    return this.makeRequest(endpoint, {
      method: 'PUT',
      body: data ? JSON.stringify(data) : undefined,
    });
  }

  async delete(endpoint: string): Promise<any> {
    return this.makeRequest(endpoint, { method: 'DELETE' });
  }
}

export const apiClient = new ApiClient();

// Authenticated API functions that can be used with useAuth
export const createAuthenticatedApi = (getAccessToken: () => string | null, refreshToken: () => Promise<void>) => {
  return {
    async request(endpoint: string, options: RequestInit = {}, retryCount = 0): Promise<any> {
      const url = `${process.env.REACT_APP_API_URL || ''}${endpoint}`;

      const token = getAccessToken();
      const config: RequestInit = {
        ...options,
        headers: {
          'Content-Type': 'application/json',
          ...(token ? { Authorization: `Bearer ${token}` } : {}),
          ...options.headers,
        },
        credentials: 'include',
      };

      try {
        const response = await fetch(url, config);

        // If we get a 401, try to refresh token once
        if (response.status === 401 && retryCount === 0) {
          try {
            await refreshToken();
            const newToken = getAccessToken();

            // Retry the request with new token
            const retryConfig = {
              ...config,
              headers: {
                ...config.headers,
                Authorization: `Bearer ${newToken}`,
              },
            };

            const retryResponse = await fetch(url, retryConfig);
            if (retryResponse.ok) {
              return await retryResponse.json();
            }
          } catch (refreshError) {
            console.error('Token refresh failed:', refreshError);
          }
        }

        const data = await response.json();

        if (!response.ok) {
          throw new Error(data.message || `HTTP error! status: ${response.status}`);
        }

        return data;
      } catch (error) {
        console.error('Authenticated API request failed:', error);
        throw error;
      }
    },

    async get(endpoint: string): Promise<any> {
      return this.request(endpoint, { method: 'GET' });
    },

    async post(endpoint: string, data?: any): Promise<any> {
      return this.request(endpoint, {
        method: 'POST',
        body: data ? JSON.stringify(data) : undefined,
      });
    },

    async put(endpoint: string, data?: any): Promise<any> {
      return this.request(endpoint, {
        method: 'PUT',
        body: data ? JSON.stringify(data) : undefined,
      });
    },

    async delete(endpoint: string): Promise<any> {
      return this.request(endpoint, { method: 'DELETE' });
    },
  };
};
