import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';

interface User {
  _id: string;
  name: string;
  email: string;
  currency: string;
}

interface AuthContextType {
  user: User | null;
  accessToken: string | null;
  isLoading: boolean;
  login: (email: string, password: string) => Promise<void>;
  logout: () => Promise<void>;
  refreshToken: () => Promise<void>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

interface AuthProviderProps {
  children: ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [accessToken, setAccessToken] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // Initialize auth state on app load
  useEffect(() => {
    const initializeAuth = async () => {
      try {
        // Try to refresh token on app load
        await refreshToken();
      } catch (error) {
        console.log('No valid session found');
      } finally {
        setIsLoading(false);
      }
    };

    initializeAuth();
  }, []);

  const login = async (email: string, password: string) => {
    try {
      const response = await fetch('/api/auth/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        credentials: 'include',
        body: JSON.stringify({ email, password }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || 'Login failed');
      }

      setUser(data.data.user);
      setAccessToken(data.data.accessToken);

      // Store user in localStorage for UI state persistence
      localStorage.setItem('expenseHub_user', JSON.stringify(data.data.user));

      // Initialize default categories for new users
      try {
        await initializeUserCategories(data.data.accessToken);
      } catch (error) {
        console.warn('Failed to initialize user categories:', error);
      }

    } catch (error) {
      console.error('Login error:', error);
      throw error;
    }
  };

  const logout = async () => {
    try {
      await fetch('/api/auth/logout', {
        method: 'POST',
        credentials: 'include',
      });

      // Clear client state but keep some preferences
      setUser(null);
      setAccessToken(null);
      localStorage.removeItem('expenseHub_user');
      // Note: transactions are now stored on the server, so we don't clear them from localStorage

    } catch (error) {
      console.error('Logout error:', error);
      // Clear client state anyway
      setUser(null);
      setAccessToken(null);
      localStorage.removeItem('expenseHub_user');
    }
  };

  const refreshToken = async () => {
    try {
      const response = await fetch('/api/auth/refresh', {
        method: 'POST',
        credentials: 'include',
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.message || 'Token refresh failed');
      }

      setAccessToken(data.data.accessToken);

      // Try to get user from localStorage or API
      const storedUser = localStorage.getItem('expenseHub_user');
      if (storedUser) {
        setUser(JSON.parse(storedUser));
      } else {
        // Could fetch user info here if needed
        console.log('Token refreshed but no user data in localStorage');
      }

    } catch (error) {
      console.error('Token refresh error:', error);
      throw error;
    }
  };

  const value: AuthContextType = {
    user,
    accessToken,
    isLoading,
    login,
    logout,
    refreshToken,
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

// Initialize default categories for new users
const initializeUserCategories = async (accessToken: string) => {
  if (!accessToken) return;

  try {
    // Check if user already has categories
    const response = await fetch('/api/categories', {
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
      credentials: 'include',
    });

    const data = await response.json();

    if (data.success && (!data.data.categories || data.data.categories.length === 0)) {
      // Create default categories
      const defaultCategories = [
        { name: 'Bills', type: 'expense', icon: '', color: '#8B5CF6' },
        { name: 'Food', type: 'expense', icon: '', color: '#F97316' },
        { name: 'Baby', type: 'expense', icon: '', color: '#EC4899' },
        { name: 'House', type: 'expense', icon: '', color: '#3B82F6' },
        { name: 'Car', type: 'expense', icon: '', color: '#10B981' },
        { name: 'Entertainment', type: 'expense', icon: '', color: '#00D9FF' },
        { name: 'Income', type: 'income', icon: '', color: '#10B981' },
        { name: 'Salary', type: 'income', icon: '', color: '#059669' },
      ];

      // Create categories in parallel
      await Promise.all(
        defaultCategories.map(category =>
          fetch('/api/categories', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${accessToken}`,
            },
            credentials: 'include',
            body: JSON.stringify(category),
          })
        )
      );

      console.log('Default categories initialized for user');
    }
  } catch (error) {
    console.warn('Error initializing user categories:', error);
  }
};
