import React from 'react';
import './Categories.css';

interface CategoryData {
  id: string;
  name: string;
  icon: string;
  amount: number;
  percentage: number;
  color: string;
}

interface CategoriesProps {
  transactions?: Array<{
    type: string;
    category: string;
    amount: number;
  }>;
  onReset?: () => void;
}

const Categories: React.FC<CategoriesProps> = ({ transactions = [], onReset }) => {
  // Calculate actual spending per category from transactions
  const calculateCategorySpending = () => {
    const categoryTotals: { [key: string]: number } = {};

    // Only count expense transactions
    transactions
      .filter(t => t.type === 'expense')
      .forEach(transaction => {
        categoryTotals[transaction.category] = (categoryTotals[transaction.category] || 0) + transaction.amount;
      });

    return categoryTotals;
  };

  const categorySpending = calculateCategorySpending();
  const totalExpenses = Object.values(categorySpending).reduce((sum, amount) => sum + amount, 0);

  // Category definitions with colors
  const categoryDefinitions: Omit<CategoryData, 'amount' | 'percentage'>[] = [
    { id: 'bills', name: 'Bills', icon: '', color: '#8B5CF6' }, // Purple
    { id: 'food', name: 'Food', icon: '', color: '#F97316' }, // Orange
    { id: 'baby', name: 'Baby', icon: '', color: '#EC4899' }, // Pink
    { id: 'house', name: 'House', icon: '', color: '#3B82F6' }, // Blue
    { id: 'car', name: 'Car', icon: '', color: '#10B981' }, // Green
    { id: 'entertainment', name: 'Entertainment', icon: '', color: '#00D9FF' } // Cyan
  ];

  // Create categories with actual spending data
  const categories: CategoryData[] = categoryDefinitions.map(cat => {
    const amount = categorySpending[cat.id] || 0;
    const percentage = totalExpenses > 0 ? Math.round((amount / totalExpenses) * 100) : 0;

    return {
      ...cat,
      amount,
      percentage
    };
  });

  return (
    <div className="categories-section">
      <div className="categories-list">
        {categories.map((category) => (
          <div key={category.id} className="category-card">
            <div className="category-left">
              <div
                className="category-icon"
                style={{
                  background: `${category.color}20`, // Add transparency to the color
                  border: `1px solid ${category.color}30`
                }}
              >
                {category.icon}
              </div>
              <div className="category-info">
                <div className="category-name">{category.name}</div>
                <div className="category-progress">
                  <div
                    className="category-progress-bar"
                    style={{
                      width: `${category.percentage}%`,
                      background: `linear-gradient(90deg, ${category.color}, ${category.color}CC)`
                    }}
                  ></div>
                </div>
              </div>
            </div>
            <div className="category-amount">{category.amount.toLocaleString()}</div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Categories;

