import React, { useState } from 'react';
import Modal from 'react-modal';
import './AddTransactionModal.css';

// Set the app element for accessibility
Modal.setAppElement('#root');

interface AddTransactionModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (transactionData: TransactionData) => void;
}

export interface TransactionData {
  type: 'expense' | 'income';
  amount: number;
  category: string;
  description: string;
  date: string;
  id?: string;
  createdAt?: string;
}

const AddTransactionModal: React.FC<AddTransactionModalProps> = ({
  isOpen,
  onClose,
  onSubmit
}) => {
  const [transactionType, setTransactionType] = useState<'expense' | 'income'>('expense');
  const [amount, setAmount] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('bills');
  const [description, setDescription] = useState('');
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]);

  const categories = [
    { id: 'bills', name: 'Bills', icon: '', type: 'expense' },
    { id: 'food', name: 'Food', icon: '', type: 'expense' },
    { id: 'baby', name: 'Baby', icon: '', type: 'expense' },
    { id: 'house', name: 'House', icon: '', type: 'expense' },
    { id: 'car', name: 'Car', icon: '', type: 'expense' },
    { id: 'entertainment', name: 'Entertainment', icon: '', type: 'expense' },
    { id: 'income', name: 'Income', icon: '', type: 'income' },
    { id: 'salary', name: 'Salary', icon: '', type: 'income' }
  ];

  const filteredCategories = categories.filter(cat => cat.type === transactionType);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (!amount || parseFloat(amount) <= 0) {
      alert('Please enter a valid amount');
      return;
    }

    if (!selectedCategory) {
      alert('Please select a category');
      return;
    }

    const transactionData: TransactionData = {
      type: transactionType,
      amount: parseFloat(amount),
      category: selectedCategory,
      description: description.trim(),
      date
    };

    onSubmit(transactionData);

    // Reset form
    setAmount('');
    setSelectedCategory('food');
    setDescription('');
    setDate(new Date().toISOString().split('T')[0]);
  };

  const handleTypeChange = (type: 'expense' | 'income') => {
    setTransactionType(type);
    // Reset category selection when changing type
    const defaultCategory = categories.find(cat => cat.type === type)?.id || 'bills';
    setSelectedCategory(defaultCategory);
  };

  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={onClose}
      className="modal-content"
      overlayClassName="modal-overlay"
      contentLabel="Add Transaction Modal"
    >
      <div className="modal-header">
        <h2 className="modal-title">Add Transaction</h2>
        <button className="modal-close" onClick={onClose}>&times;</button>
      </div>

      <div className="modal-body">
        <form onSubmit={handleSubmit}>
          {/* Transaction Type Selector */}
          <div className="form-group">
            <div className="transaction-type-tabs">
              <button
                type="button"
                className={`type-tab ${transactionType === 'expense' ? 'active' : ''}`}
                onClick={() => handleTypeChange('expense')}
              >
                <span></span> Expense
              </button>
              <button
                type="button"
                className={`type-tab ${transactionType === 'income' ? 'active' : ''}`}
                onClick={() => handleTypeChange('income')}
              >
                <span></span> Income
              </button>
            </div>
          </div>

          {/* Amount Input */}
          <div className="form-group">
            <label className="form-label">Amount</label>
            <div className="amount-input-group">
              <span className="currency-symbol"></span>
              <input
                type="number"
                className="form-input amount-input"
                placeholder="0.00"
                min="0"
                step="0.01"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                required
              />
            </div>
          </div>

          {/* Category Selection */}
          <div className="form-group">
            <label className="form-label">Category</label>
            <div className="category-grid">
              {filteredCategories.map((category) => (
                <button
                  key={category.id}
                  type="button"
                  className={`category-btn ${selectedCategory === category.id ? 'active' : ''}`}
                  onClick={() => setSelectedCategory(category.id)}
                >
                  <span className="category-icon-btn">{category.icon}</span>
                  <span>{category.name}</span>
                </button>
              ))}
            </div>
          </div>

          {/* Description */}
          <div className="form-group">
            <label className="form-label">Description</label>
            <input
              type="text"
              className="form-input"
              placeholder="Add a note..."
              value={description}
              onChange={(e) => setDescription(e.target.value)}
            />
          </div>

          {/* Date */}
          <div className="form-group">
            <label className="form-label">Date</label>
            <input
              type="date"
              className="form-input"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              required
            />
          </div>

          {/* Footer Buttons */}
          <div className="form-footer">
            <button type="submit" className="btn-modal btn-modal-submit">
              Add Transaction
            </button>
            <button type="button" className="btn-modal btn-modal-cancel" onClick={onClose}>
              Cancel
            </button>
          </div>
        </form>
      </div>
    </Modal>
  );
};

export default AddTransactionModal;
