import React, { useState } from 'react';
import './SpendingChart.css';

type PeriodType = 'daily' | 'monthly' | 'yearly';

interface TransactionData {
  type: string;
  category: string;
  amount: number;
  date: string;
}

interface SpendingChartProps {
  transactions: TransactionData[];
}

const SpendingChart: React.FC<SpendingChartProps> = ({ transactions }) => {
  const [selectedPeriod, setSelectedPeriod] = useState<PeriodType>('monthly');

  // Calculate spending data from transactions
  const calculateSpendingData = () => {
    const now = new Date();
    const expenseTransactions = transactions.filter(t => t && t.type === 'expense');

    const spendingData = {
      daily: {
        labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
        data: [0, 0, 0, 0, 0, 0, 0],
        currentValue: 0
      },
      monthly: {
        labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul'],
        data: [0, 0, 0, 0, 0, 0, 0],
        currentValue: 0
      },
      yearly: {
        labels: ['2020', '2021', '2022', '2023', '2024', '2025', '2026'],
        data: [0, 0, 0, 0, 0, 0, 0],
        currentValue: 0
      }
    };

    // Calculate daily spending (last 7 days)
    const sevenDaysAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
    expenseTransactions
      .filter(t => t.date && !isNaN(Date.parse(t.date)))
      .filter(t => new Date(t.date) >= sevenDaysAgo)
      .forEach(transaction => {
        try {
          const date = new Date(transaction.date);
          const dayIndex = (date.getDay() + 6) % 7; // Convert Sunday=0 to Monday=0
          spendingData.daily.data[dayIndex] += transaction.amount || 0;
        } catch (error) {
          console.warn('Invalid date in transaction:', transaction);
        }
      });
    spendingData.daily.currentValue = spendingData.daily.data.reduce((sum, val) => sum + val, 0);

    // Calculate monthly spending (last 7 months)
    const sevenMonthsAgo = new Date(now.getFullYear(), now.getMonth() - 6, 1);
    expenseTransactions
      .filter(t => t.date && !isNaN(Date.parse(t.date)))
      .filter(t => new Date(t.date) >= sevenMonthsAgo)
      .forEach(transaction => {
        try {
          const date = new Date(transaction.date);
          const monthIndex = date.getMonth();
          spendingData.monthly.data[monthIndex] += transaction.amount || 0;
        } catch (error) {
          console.warn('Invalid date in transaction:', transaction);
        }
      });
    spendingData.monthly.currentValue = spendingData.monthly.data.reduce((sum, val) => sum + val, 0);

    // Calculate yearly spending (last 7 years)
    const sevenYearsAgo = now.getFullYear() - 6;
    expenseTransactions
      .filter(t => t.date && !isNaN(Date.parse(t.date)))
      .filter(t => {
        const year = new Date(t.date).getFullYear();
        return year >= sevenYearsAgo && year <= now.getFullYear();
      })
      .forEach(transaction => {
        try {
          const year = new Date(transaction.date).getFullYear();
          const yearIndex = year - sevenYearsAgo;
          if (yearIndex >= 0 && yearIndex < 7) {
            spendingData.yearly.data[yearIndex] += transaction.amount || 0;
          }
        } catch (error) {
          console.warn('Invalid date in transaction:', transaction);
        }
      });
    spendingData.yearly.currentValue = spendingData.yearly.data.reduce((sum, val) => sum + val, 0);

    return spendingData;
  };

  const spendingData = calculateSpendingData();
  const currentData = spendingData[selectedPeriod] || {
    labels: [],
    data: [],
    currentValue: 0
  };

  // Find the index of the current/highlighted bar (the one with the highest value)
  const maxValue = currentData.data.length > 0 ? Math.max(...currentData.data) : 0;
  const highlightedIndex = maxValue > 0 ? currentData.data.indexOf(maxValue) : -1;

  const handlePeriodChange = (period: PeriodType) => {
    setSelectedPeriod(period);
  };

  return (
    <div className="spending-chart-section">
      <div className="section-title">
        <span>Spending Overview</span>
        <div className="period-selector">
          <button
            className={`period-btn ${selectedPeriod === 'daily' ? 'active' : ''}`}
            onClick={() => handlePeriodChange('daily')}
          >
            Daily
          </button>
          <button
            className={`period-btn ${selectedPeriod === 'monthly' ? 'active' : ''}`}
            onClick={() => handlePeriodChange('monthly')}
          >
            Monthly
          </button>
          <button
            className={`period-btn ${selectedPeriod === 'yearly' ? 'active' : ''}`}
            onClick={() => handlePeriodChange('yearly')}
          >
            Yearly
          </button>
        </div>
      </div>

      <div className="chart-card">
        <div className="chart-value">{currentData.currentValue.toLocaleString()}</div>
        <div className="chart-container">
          <div className="simple-chart">
            <div className="chart-bars">
              {currentData.labels.map((label, index) => {
                const value = currentData.data[index] || 0;
                const percentage = maxValue > 0 ? (value / maxValue) * 100 : 0;
                const isHighlighted = index === highlightedIndex;

                return (
                  <div key={label} className="chart-bar-container">
                    <div
                      className={`chart-bar ${isHighlighted ? 'highlighted' : ''}`}
                      style={{
                        height: `${Math.max(percentage, 2)}%`,
                        backgroundColor: isHighlighted ? '#00D9FF' : '#8B93A8'
                      }}
                      title={`${label}: ${value}`}
                    />
                    <div className="chart-label">{label}</div>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SpendingChart;